.header {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 10%;
  align-items: center;
  border: 1px solid;
}

.header-img {
  width: 50px;
  height: 30px;
  margin-left: 20px;
}

.p1{
    margin-left: 500px;
    font-size: 16px;
    color: #127749
}
.p2{
    font-size: 16px;
}
.p3{
    font-size: 16px;
}
.header-btn {
  width: 120px;
  height: 40px;
  margin-right: 20px;
  border-radius: 20px;
  border-color: #127749;
  font-size: 16px;
  color: #127749
  
}
.header-btn:hover {
    background-color: #127749;
    color: black;
  }

@media screen and (max-width: 767px) {
  .header {
    margin-top: 5px;
    flex-wrap: wrap;
    flex-direction: column;
    width:100%;
    height: 1%;
    justify-content: center;
    align-items: center;
  }
  .header-btn {
    width: 25%;
    height: 5%;
    margin-right: 0px;
    border-radius: 5px;
    border-color: "#127749";
    font-size: 8px;
    color: "#127749";
margin-bottom: 5px;
  }
  .header-img {
    width: 60px;
    height: 40px;
  }
  .p1{
    margin-Left: 0px;
  font-size: 8px;
  }
.p2{
  font-size: 8px;
}
.p3{
    font-size: 8px;
  }
}

@media screen and (max-width: 1140px) {
    .header {
      width:100%;
      height: 5%;
      justify-content: space-between;
    }
    .header-btn {
      width: 60px;
      height: 10%;
      margin-right: 10px;
      border-radius: 5px;
      border-color: "#127749";
      font-size: 12px;
      color: "#127749"
    }
    .header-img {
      margin-left:0px;
    }
    .p1{
    margin-Left: center;
    font-size: 12px;
  
    }
  .p2{
    font-size: 12px;
  }
  .p3{
      font-size: 12px;
    }
  }
  
  