
.footer {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 1%;
  align-items: center;
}

.header-img {
  width: 50px;
  height: 30px;
  margin-left: 20px;
}

.footer-cont {
  border: 1px solid;
  height: 50px;
  width: 100%;
  margin-top: 50px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.footer-img {
  margin-top: 100px;
}

.p1 {
  color: #127749;
  font-size: 16px;
}
.p2 {
  font-size: 16px;
}
.p3 {
  font-size: 16px;
}

.p4 {
  font-size: 16px;
}

@media screen and (max-width: 767px) {
  .footer {
    margin-top: 5px;
    flex-wrap: wrap;
    flex-direction: column;
    width: 100%;
    height: 1%;
    justify-content: center;
    align-items: center;
  }
  .footer-img {
    width: 60px;
    height: 40px;
  }
  .footer-cont {
    flex-direction: column;
  }
  .p1 {
   
    width: 100%;
    border: 1px solid;
    margin-left: 0px;
    font-size: 8px;
  }
  .p2 {
    width: 100%;
    border: 1px solid;
    font-size: 8px;
  }
  .p3 {
    width: 100%;
    border: 1px solid;
    font-size: 8px;
  }
  .p4 {
    width: 100%;
    border: 1px solid;
    font-size: 8px;
  }
}

@media screen and (max-width: 1140px) {
  .footer {
    width: 100%;
    height: 5%;
    justify-content: space-between;
    flex-direction: column;
  }

  .footer-img {
    margin-left: 0px;
  }
  .p1 {
    margin-left: center;
    font-size: 12px;
  }
  .p2 {
    font-size: 12px;
  }
  .p3 {
    font-size: 12px;
  }
}
