.carousel{
    width: 100%;
    margin-top: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.slide{
    width:150px;
    height: 100px;
}

@media screen and (max-width: 767px) {
    .carousel{
        flex-direction: column;
        flex-wrap: wrap;

    }
    .btn-car{
        margin-bottom: 20px;

    }
    .btn-car-next{
        margin-top: 20px;
    }
  }
  
  @media screen and (max-width: 1140px) {
    .carousel{
        flex-wrap: wrap;
        flex-direction: column;
        margin-top: 10px;
    }
    .btn-car{
        margin-bottom: 20px;
    }
    .btn-car-next{
        margin-top: 20px;

    }
    }